	import { mapState } from 'vuex'
	import { mapGetters } from 'vuex'
	export default {
		data() {
			return {
				fullName:'',
                roomInfo:'',
				idType:'',
                oa_id:''
		    };
		},
        created:function () {
	        //
	        this.fullName = localStorage.FULLNAME;
	        //
	        this.roomInfo = localStorage.ROOM_INFO;
	        //
	        this.idType = localStorage.idType;
	        //id
	        this.oa_id = localStorage.oa_id;
        },
		methods: {
			logOut:function () {
				//tokenfullname
				this.$dialog.confirm({
					title: ' ',
					mes: this.language.common.loginTxt,
					opts: [
						{
							txt: this.language.common.cancel,
							color: false
						},
						{
							txt: this.language.common.sure,
							color: '#f0c366',
							callback: () => {

                                let obj={
                                    userType:'staff',
                                    userId:localStorage.userId,
                                    hotelId: [
                                        'hotel_'+localStorage.HOTELID,
                                        'group_1',
                                        localStorage.LANGUAGE
                                    ]
                                }

                                if(/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)){
                                    removeAllTagAlias(obj.userType,obj.userId,obj.hotelId)
                                } else {
                                    removeMessageTag(obj.userType,obj.userId,obj.hotelId)
                                }

                                localStorage.TOKEN = '';
                                localStorage.FULLNAME = '';
                                localStorage.HOTELID = '';
                                localStorage.idType = '';
                                localStorage.searchFlag = '';
                                this.$router.push('/unlogin');

							}
						}
					]
				});
			},
			goGsm:function () {
				let _this = this;
				let params = {
					token:localStorage.TOKEN
				}
				this.$store.dispatch('getUsers', params).then(function (res) {
					_this.gsmSrc =  res.data.serviceUrl
                    if(/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)){
                        openFile(res.data.serviceUrl)
                    } else {
                        openUrl(res.data.serviceUrl,'gsm')
                    }
				})
//				this.$router.replace({path:'/gsm'})
			},
			goBill:function () {
				this.$router.push({path:'/mybill'})
			},
            goInvoices:function () {
                this.$router.push({path:'/invoices'})
            },
            changePwd: function() {
                let _this = this;
                let alobj = new alertLanguage();
                let obj = alobj.getAlertMsg(localStorage.LANGUAGE);
                let sureBnt = obj.sureBnt;
                let cancelBnt = obj.cancelBnt;
                let dialog = window.YDUI.dialog;
                let pwdHtml = `
                <div>
                    :<input id="pwdNum1" type="number" oninput="if(value.length>6)value=value.slice(0,6)" style="width:80%;">
                </div>
                <div>
                    :<input id="pwdNum2" type="number" oninput="if(value.length>6)value=value.slice(0,6)" style="width:80%;">
                </div>
                `;
                let title = '';
                let msg1 = "6!";
                let msg2 = "6!";
                let msg3 = "token!";
                let msg4 = "!";
                let msg5 = "!";
                ///
                if(localStorage.LANGUAGE!='zh'){
                    pwdHtml = `
                    <div>
                        Old password:<input id="pwdNum1" type="number" oninput="if(value.length>6)value=value.slice(0,6)" style="width:80%;">
                    </div>
                    <div>
                        New password:<input id="pwdNum2" type="number" oninput="if(value.length>6)value=value.slice(0,6)" style="width:80%;">
                    </div>
                    `;
                    title = 'Please enter your password';
                    msg1 = "Old password is 6 digits!";
                    msg2 = "New password is 6 digits!";
                    msg3 = "Token expired, please log in again!";
                    msg4 = "Successfully set up!";
                    msg5 = "Old password entered incorrectly!";
                }
                dialog.confirm(title,pwdHtml, [
                    {
                        txt: sureBnt,
                        color: false,
                        stay: true, //
                        callback: function () {
                            let pwd1 = $("#pwdNum1").val();
                            let pwd2 = $("#pwdNum2").val();
                            if(!/^[0-9]{6}$/.test(pwd1)){
                                dialog.alert(msg1);
                                $("#pwdNum1").val("");
                                $("#pwdNum1").focus();
                                return;
                            }
                            if(!/^[0-9]{6}$/.test(pwd2)){
                                dialog.alert(msg2);
                                $("#pwdNum2").val("");
                                $("#pwdNum2").focus();
                                return;
                            }
                            dialog.loading.open('Loading');
                            //
                            let pinParams = {
                                token: localStorage.TOKEN,
                                pin: pwd1
                            };
                            _this.$store.dispatch('checkPin', pinParams).then(function (res) {
                                if(res.code == 0){
                                    //
                                    let setParams = {
                                        token: localStorage.TOKEN,
                                        old_pin:pwd1,
                                        pin: pwd2
                                    };
                                    _this.$store.dispatch('setPin', setParams).then(function (res) {
                                        if(res.code == 0){
                                            dialog.alert(msg4);
                                        }else if(res.code == 1){
                                            dialog.alert(msg3);
                                        }else{
                                            dialog.alert(res.msg);
                                        }
                                        dialog.loading.close();
                                        $("#pwdNum1").val("");
                                        $("#pwdNum2").val("");
                                        $("#YDUI_CONFRIM").hide();
                                    });
                                }else if(res.code == 1){
                                    dialog.alert(msg3);
                                    dialog.loading.close();
                                    $("#pwdNum1").val("");
                                    $("#pwdNum2").val("");
                                    $("#YDUI_CONFRIM").hide();
                                }else if(res.code == 2){
                                    dialog.alert(msg5);
                                    dialog.loading.close();
                                    $("#pwdNum1").focus();
                                }else{
                                    dialog.alert(res.msg);
                                    dialog.loading.close();
                                    $("#pwdNum1").focus();
                                }
                            });
                        }
                    },
                    {
                        txt: cancelBnt,
                        color: false,
                        callback: function () {
                            $("#pwdNum1").val("");
                            $("#pwdNum2").val("");
                        }
                    }
                ]);
                $("#pwdNum1").focus();
            }
		},
		computed: {
			...mapState({
				language: state => state.language.language
			})
		},

	};



